<template>
 <div class="py-24 pt-48 text-center basis-full">
    <h3
      class="text-3xl font-bold mb-5 relative after:absolute inline-block after:w-[80%] after:h-0.5 after:bg-slate-800 after:-bottom-2 after:left-2/4 after:-translate-x-2/4">
      {{ title[0] }}</h3>
    <p class="text-gray-400">{{ desc[0] }}</p>
  </div>
  <hr>
  <!-- firebase   -->
  <div v-for="e in dataList" :key="e">
    <h3>firebase </h3>
    <div>{{ e.name }}</div>
    <div>{{ e.price }}</div>
  </div>
  <!-- vuex  commit    -->
  <div>
    <h3>vuex      2   </h3>
    <div class="text-2xl">number: {{ $store.state.number }}</div>
    <button @click="$store.commit('numberPlus')" class="text-6xl">+</button>
    <button @click="$store.commit('numberMinus')" class="text-6xl">-</button>
  </div>
</template>

<script>
  import {db} from '@/firebase'

  export default {
    name: 'ProductView_1',
    props: {
      title: Array,
      desc: Array
    },
    data() {
      return {
        dataList : []
      }
    },
    mounted() {
      // db.('').get().then.(()=>{})
      //      Cloud Firestore    ->  allow read, write: if true;  
      db.collection('product').get().then((data)=> {
        data.forEach((e)=> { 
          // console.log(e.data())
          this.dataList.push(e.data())
        })
      })
    },
  }
</script>

