<template>
  <div class="basis-full">
    <div class="flex justify-between max-w-7xl mx-auto my-6 py-1 border-b">
      <h3 class="text-3xl font-bold">
        {{ BoardContent.title }}
        <span class="text-base">{{ BoardContent.name }}</span>
      </h3>
      <div class="flex">
        <div class="mr-2"> : {{ BoardContent.hit }}</div>
        <div @click="goodChk" class="mr-2"> : {{ BoardContent.good }}</div>
        <div @click="unGoodChk" class="mr-2">: {{ BoardContent.bad }}</div>
      </div>
    </div>
    <div class="mt-8 pb-5 border-b">
      <p class="my-2">{{ BoardContent.content }}</p>
    </div>
    <div class="flex justify-between pb-24 gap-x-5 mt-5">
      <router-link to="/customerservice/online/list" 
        class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
        
      </router-link>
      <div class="flex gap-x-5">
        <router-link to="/customerservice/online/modify" 
          class="bg-blue-400 hover:bg-blue-600 focus:ring-blue-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
          
        </router-link>
        <button 
          @click="Delete"
          class="bg-red-400 hover:bg-red-600 focus:ring-red-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm">
          
        </button>
      </div>
    </div>
  </div>
</template>

<script>
   import { db } from '@/firebase'

  export default {
    name: 'OnlineRead',
    data() {
      return {
        BoardContent: [],
      }
    },
    mounted() {
      if (this.$route.query.docId == null) {
        this.$router.replace('/customerservice/online/list')
      }
      db.collection('online').doc(this.$route.query.docId).get().then((data) => {
        this.BoardContent = data.data()

        //     
        // if(data.data().isUpdate){return}

        db.collection('online').doc(this.$route.query.docId).update({
          hit: Number(data.data().hit) + 1,
          // isUpdate: true
        })
        console.log(typeof this.BoardContent.hit)
        this.BoardContent.hit = this.BoardContent.hit + 1
      })
    },
    methods: {
      Delete() {
        let msg = confirm('     . \r\r ?')
        if (msg) {
          db.collection('online').doc(this.$route.query.docId).delete().then(() => {
            alert(' .');
            this.$router.replace('/customerservice/online')
          })
        }
      },
      goodChk() {
        if (this.BoardContent.goodorbadChk) {
          alert('    .')
          return
        }
        db.collection('online').doc(this.$route.query.docId).update({
          good: this.BoardContent.good + 1,
          goodorbadChk: true
        }).then(() => {
          db.collection('online').doc(this.$route.query.docId).get().then((data) => {
            this.BoardContent = data.data()
          })
        })
      },
      //             
      //   goodorbadChk  db  db     
      unGoodChk() {
        if (this.BoardContent.goodorbadChk) {
          alert('    .')
          return
        }
        this.BoardContent.bad = this.BoardContent.bad + 1
        db.collection('online').doc(this.$route.query.docId).update({
          bad: this.BoardContent.bad,
          goodorbadChk: true

        })
      }
    },
  }
</script>